import React from "react";


function Math () {
    let result = 0;

    switch(PaymentResponse.operator) {
        case "+":
            result = props.num1 + props.num2;
            break;
        case "-":
            result = props.num1 - props.num2;
            break;
        case "*":
            result = props.num1 * props.num2;
            break;
        case "/":
            result = props.num1 / props.num2;
            break;
        default:
            NaN   
    }
    return result;
}

export default Math;